@import "../mixins/variables";

.navbar {
    background-color: #fff;
    padding-left: 1rem 2rem;
    padding-right: 1rem;
    padding-bottom: 1rem;
    padding-top: 1rem;
    height: 6rem;
    @include respond(phone) {
        padding-right: 0;
        
    }
 
    &__flolog-logo{
        height: 30px;
        margin-left: 4rem;
        padding-bottom: 1rem;

        @include respond(phone) {
            margin-left: 1.5rem;
            
        }
      }
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style: none;
        height: 100%;
        padding: 0 4rem;

        @include respond(tab-port) {
            padding: 0 3rem;
        }

        &__item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 1.8rem;
            margin: 0 20px;
         

            @include respond(tab-port) {
                display: none;
            }
        }
    }

    &__link {
        color: $primary;
        font-weight: 500;
        font-size: 1.5rem;
        text-decoration: none;


        &:hover {
            color: rgb(248, 205, 232);
        }

    }

    &__icons > img{
        height: 5px;
    }

    &__gestlinks{
        margin-left: 30px;
        font-size: 1.9rem;
    }
    &__logobtn{
        
        &__btn{
            display: none;
        

            @include respond(tab-port) {
                display: inline-flex;
                color: rgb(49, 49, 49);
                font-size: 2rem;
                font-weight: 600;
                margin-left: 2rem;

                &:hover {
                    height: 70%;
                    margin: 15% 0;
                }
            }
        }
    }
    &__logo {
        height: 6rem;
     

        @include respond(tab-port) {
            margin: 0 auto;
        }
        
        @include respond(phone) {
            width: 2rem;
        }
    }
   

    &__button {
        display: none;

        @include respond(tab-port) {
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            height: 65%;
            margin: 17.5% 0;
            width: 5rem;
            cursor: pointer;
            

            &:hover {
                height: 70%;
                margin: 15% 0;
            }

            &__component {
                display: none;

                @include respond(tab-port) {
                    display: block;
                    height: 0.3rem;
                    width: 100%;
                    background-color: #333;
                }
            }
        }
    }
    &__auth-link{
        cursor: pointer;
        font-size: 1.6rem;
    
    }
    
}

